.projectBlock {
    min-height: 100vh;
    background-color: lightcyan;
    flex-wrap: wrap;
}

.projectContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: cadetblue;
}

.projects {
    border: 1px solid red;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
